#oloader_loader_0_1,#oloader_canvas_0_1{
	background: transparent !important;
	z-index: 9999 !important;
	background: none !important;

}
#oloader_overlay_0_1{
	background: transparent !important;
}
#ownage_page_loader_text{
	color: $blue !important;
	font-family: $font-en;
}

.loading{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 99;
	background: white;
	@include flex(center,center,wrap);
	transition: all .7s ease;
	padding-bottom: 250px;
	&-img{
		display: block;
		width: 150px;

	}
}

.is-hello{
	&.active{
		.loading{
			opacity: 0;
			visibility: hidden;
			z-index: -1;
		}
	}
	.loading{
		.bar{
			width: 100%;
		}
	}
	.mv-block{
		opacity: 1;
		visibility: visible;
		transition: all .7s ease;
		transition-delay: 1s;
	}
	.mv-links{
		opacity: 1;
		visibility: visible;
		transition: all .7s ease;
		transition-delay: 1.3s;
	}
}